let page = 1
export default {
    onLoad(){
        page = 1
        this.getTxmxlist()
    },
    data(){
        return {
            recordList:[],
            loadingType:0
        }
    },
    onReachBottom:function(){
        // 上拉加载更多
        if(this.loadingType == 1 || this.loadingType == 2) { return false }
        this.loadingType = 1

        page++
        this.getTxmxlist()
	},
    methods: {
        getTxmxlist(){
            this.$http.getTxmxlist({page:page}).then(res=>{
                if(res.status == 200) {
                    if(res.data.length == 0) {
                        this.loadingType = 2
                    }
                    this.recordList = [...this.recordList,...res.data]
                    this.loadingType = 0
                }
            })
        }
    },
}
